import PlotPolygonBase from './base/PlotPolygonBase';
import getArrowHead from './math/arrowHead.js';
const Tool = XE.Tool;

//()
class GeoSimpleArrow extends PlotPolygonBase {
    constructor(earth, guid) {
        super(earth, guid);
        
        this._onlyMove = true;
        
        this._polygonPositions = [];
        this._fixedPositionsNum = 2;

        this.disposers.push(XE.MVVM.watch( ()=>{
            return {
                showType: this.showType,
            }
        }, ()=>this.changeShowType()))

        this.disposers.push(XE.MVVM.watch(() =>
        {
            return {
                minWidthScale : this.minWidthScale,
                dynamicWidthScale : this.dynamicWidthScale,
                arrowMaxHeightScale : this.arrowMaxHeightScale,
                arrowWingWidthScale : this.arrowWingWidthScale
            };
        }, () => {
            this.change(this.positions, this.minWidthScale, this.dynamicWidthScale, this.arrowMaxHeightScale, this.arrowWingWidthScale);
        }));

        this.disposers.push(XE.MVVM.watch(() => {
            return [...this.positions.map(e => [...e])];
        }, () => {
            this.change(this.positions, this.minWidthScale, this.dynamicWidthScale, this.arrowMaxHeightScale, this.arrowWingWidthScale);
        }));

    }

    changeShowType(){
        let param = params[this.showType];
        Object.keys( param).forEach( key=>{
            this[key] = param[key];
        })
    }
    
    change(){
        
        const l = this.positions.length;
        if(l < 2 || isEquals(this.positions[0],this.positions[1])){
            return;
        }
        
        let positionsC = this.positions.map( e=>{
            return Cesium.Cartesian3.fromRadians( ...e, null, new Cesium.Cartesian3())
        });
        
        this._polygonPositions.length = 0;
        
        //
        let length = Tool.Math.geoDistance( ...this.positions);
        let heading = Tool.Math.hpr(...this.positions)[0];
        let neckCenter = Tool.Math.geoMove( this.positions[0], heading, length*(1-this.arrowMaxHeightScale));

        let maxWidthScale = (this.dynamicWidthScale+this.minWidthScale)/2;
        let minWidthScale = this.minWidthScale/2;
        let tailSide = [
            Tool.Math.geoMove( this.positions[0], heading + Cesium.Math.PI_OVER_TWO, length*maxWidthScale),
            Tool.Math.geoMove( this.positions[0], heading - Cesium.Math.PI_OVER_TWO, length*maxWidthScale)
        ];
        
        let neckHeading = Tool.Math.hpr(neckCenter, this.positions[1])[0];
        let neckSide = [
            Tool.Math.geoMove( neckCenter, neckHeading + Cesium.Math.PI_OVER_TWO, length*minWidthScale),
            Tool.Math.geoMove( neckCenter, neckHeading - Cesium.Math.PI_OVER_TWO, length*minWidthScale)
        ];

        let tailHeading = [
            Tool.Math.hpr(tailSide[0], neckSide[0])[0],
            Tool.Math.hpr(tailSide[1], neckSide[1])[0]
        ];
        let distances = [
            Tool.Math.geoDistance(neckSide[0], tailSide[0]),
            Tool.Math.geoDistance(neckSide[1], tailSide[1])
        ];
        //
        let positions = [[],[]];
        for( let i = 0; i < 20 ; i++){
            positions[0].push( Tool.Math.geoMove( tailSide[0], tailHeading[0], i * distances[0]/20));
            positions[1].push( Tool.Math.geoMove( tailSide[1], tailHeading[1], i * distances[1]/20));
        }
        if( this.tail){
            //
            let tailCenter = Tool.Math.geoMove( this.positions[0], heading, this.tailHeightScale * length);
            this._polygonPositions.push( tailCenter[0], tailCenter[1]);
        }
        positions[0].forEach( e=>{
            
            this._polygonPositions.push( e[0],e[1]);
        });

        //
        let arrowHead = [];
        getArrowHead( length, this.arrowMaxHeightScale, this.minWidthScale/2, this.arrowWingWidthScale, 
            positionsC, this.positions, arrowHead);
        for(let i = arrowHead.length-1; i >= 0; i--){
            this._polygonPositions.push( arrowHead[i][0], arrowHead[i][1]);
        }

        for(let i = positions[1].length-1; i >= 0; i--){
            this._polygonPositions.push( positions[1][i][0], positions[1][i][1]);
        }
        
        this._polygon.positions = this._polygonPositions;
        this._polygon.height = this.positions[0][2];

        this._polygonShow = true;
    }
}

const params = {
    forkedTail:{//
        minWidthScale: 0.06,
        dynamicWidthScale: 0.1,
        arrowMaxHeightScale: 0.15,
        arrowWingWidthScale: 2.0,
        tail: true,
    },
    cusp:{//
        minWidthScale: 0.06,
        dynamicWidthScale: 0.1,
        arrowMaxHeightScale: 0.15,
        arrowWingWidthScale: 2.0,
        tail: false,
    },
    parallel:{//
        minWidthScale: 0.12,
        dynamicWidthScale: 0.0,
        arrowMaxHeightScale: 0.08,
        arrowWingWidthScale: 2.0,
        tail: false,
    }
}

GeoSimpleArrow.defaultOptions = {
    showType: "forkedTail",
    positions: [],
    tailHeightScale: 0.15,
    ...params.forkedTail,
};

GeoSimpleArrow.registerType(GeoSimpleArrow, 'GeoSimpleArrow');
export default GeoSimpleArrow;

//
let index;
function isEquals( left, right){
    if(!left || !right){
        return false;
    }
    index = 0;
    for(let i=0; i<left.length; i++){
        index = left[i] == right[i] ? 0 : 1;
        if(index > 0){
            return false;
        }
    }
    return true;
}