class PlotPolylineBase extends XE.Obj.PlotBase {
    constructor(earth, guid) {
        super(earth, guid);

        this._onlyMove = false;
        this._fixedPositionsNum = undefined;

        XE.MVVM.extend(this, {
            _polylineShow: true,
        });

        this._polyline = new XE.Obj.Polyline(earth);
        this.disposers.push(() => {
            this._polyline = this._polyline && this._polyline.destroy();
        });
        this._polyline.show = this._polylineShow && this.show;

        XE.Earth.Interaction.Picking.registerPickingParent(this._polyline, this);
        this._labels = [];
        this._totalLabel = undefined;
        function polylineTotalLength(points) {
            var totalDistance = 0.0;
            var pl = points.length;
            for (var i = 0; i < pl - 1; ++i) {
                totalDistance += Cesium.Cartesian3.distance(points[i], points[i + 1]);
            }

            return totalDistance;
        }
        function createPointLabelsCallback(positions, callback) {
            var pl = positions.length;
            for (var i = 0; i < pl - 1; ++i) {
                var p0 = positions[i];
                var p1 = positions[i + 1];
                var dis = Cesium.Cartesian3.distance(p0, p1);
                var middlePt = Cesium.Cartesian3.lerp(p0, p1, 0.5, new Cesium.Cartesian3());
                var text = changeDisUnit(dis)
                callback(i, dis, middlePt, text);
            }
        }
        function createPointLabel(viewer, position, text) {
            if (!position) return
            var dis = Cesium.Cartesian3.distance(viewer.camera.positionWC, position);

            var oLabelEntity = {
                name: "",
                position: position,
                clampToGround: true,
                attachPolygon: true,
                label: {
                    text: text,
                    // translucencyByDistance: new Cesium.NearFarScalar(1.5e2, 2.0, 1.5e5, 0),
                    translucencyByDistance: new Cesium.NearFarScalar(
                        dis * 5.0,
                        2.0,
                        dis * 30.0,
                        0
                    ),
                    style: Cesium.LabelStyle.FILL_AND_OUTLINE,
                    font: "35px ",
                    fillColor: Cesium.Color.WHITE.withAlpha(0.9), // withAlpha
                    outlineColor: Cesium.Color.BLACK.withAlpha(0.9),
                    outlineWidth: 6,
                    disableDepthTestDistance: 1000000000,
                    scale: 0.5,
                    pixelOffset: new Cesium.Cartesian2(0, -10),
                    // showBackground:true,
                    // backgroundColor:new Cesium.Color.fromCssColorString("rgba(0, 0, 0, 0.7)"),
                    // backgroundPadding:new Cesium.Cartesian2(10, 10),
                    verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
                },
                // point: {
                //     color: new Cesium.Color(1.0, 0.0, 0.0, 0.8),
                //     pixelSize: 8,
                //     perPositionHeight: false,
                //     disableDepthTestDistance: 1000000000,
                // },
            };

            return earth.czm.viewer.entities.add(oLabelEntity);
        }
        function setPointLabel(entity, position, text) {
            entity.position = position;
            entity.label.text = text;
        }
        const updatePolyline = () => {
            this._polyline.show = this._polylineShow && this.show;
            this._polyline.width = this.width;
            this._polyline.material.XbsjColorMaterial.color = this.color;
            this._polyline.ground = this.ground;
            this._polyline.depthTest = this.depthTest;
            this._polyline.minShowHeight = this.minShowHeight;
            this._polyline.maxShowHeight = this.maxShowHeight;
            const cartesians = this.positions.map(p => Cesium.Cartesian3.fromRadians(...p));
            this._labels.forEach((e) => (e.show = (this.showLength && this.show)));
            if (this._totalLabel) {
                this._totalLabel.show = this.showLength && this.show;
            }
            // if (!this.creating && this._labels.length > 0) {
            //     this._labels.pop();
            // }

            //
            createPointLabelsCallback(
                cartesians,
                (i, dis, middlePt, text) => {
                    if (typeof this._labels[i] === "undefined") {
                        this._labels[i] = createPointLabel(
                            earth.czm.viewer,
                            middlePt,
                            text
                        );
                    } else {
                        setPointLabel(this._labels[i], middlePt, text);
                    }
                }
            );
            //
            let totalLength = polylineTotalLength(cartesians);
            if (typeof this._totalLabel === "undefined") {
                this._totalLabel = createPointLabel(
                    earth.czm.viewer,
                    cartesians[0],
                    ":" + changeDisUnit(totalLength)
                );
            } else {
                setPointLabel(
                    this._totalLabel,
                    cartesians[0],
                    ":" + changeDisUnit(totalLength)
                );
            }

            let pl = this.positions.length;
            for (
                var i = pl - 1 < 0 ? 0 : pl - 1;
                i < this._labels.length;
                ++i
            ) {
                this._labels[i].show = false;
            }
        }
        function changeDisUnit(dis) {
            if (dis > 1000) {
                return (dis / 1000).toFixed(3) + ''
            } else {
                return dis.toFixed(3) + ''
            }
        }
        updatePolyline();
        this.disposers.push(XE.MVVM.watch(() => {
            return {
                show: this.show,
                polylineShow: this._polylineShow,
                width: this.width,
                color: [...this.color],
                ground: this.ground,
                depthTest: this.depthTest,
                minShowHeight: this.minShowHeight,
                maxShowHeight: this.maxShowHeight,
                positions: [...this.positions],
                showLength: this.showLength
            }
        }, () => updatePolyline()),
            () => {
                this._labels.forEach((e) =>
                    earth.czm.viewer.entities.remove(e)
                );
                earth.czm.viewer.entities.remove(this._totalLabel);
            }
        );
    }

    get onlyMove() {
        return this._onlyMove;
    }

    get fixedPositionsNum() {
        return this._fixedPositionsNum;
    }

    flyTo() {
        this._polyline.flyTo();
    }
}

PlotPolylineBase.defaultOptions = {
    positions: [],
    show: true,
    width: 5.0,
    color: [0, 1, 0, 1],
    depthTest: false,
    minShowHeight: -Number.MAX_VALUE,
    maxShowHeight: Number.MAX_VALUE,
    showLength: false,
};

export default PlotPolylineBase;
