import PlotPolygonBase from './base/PlotPolygonBase';
import getArrowHead from './math/arrowHead.js';

const Tool = XE.Tool;

//
class GeoArrow extends PlotPolygonBase {
    constructor(earth, guid) {
        super(earth, guid);
        
        this._onlyMove = true;
        
        this._polygonPositions = [];

        this.a = {b:{c:5},bb:5};
        XE.MVVM.extend(this, { a: this.a }, { a: XE.MVVM.reaction.observable.ref });
        this.disposers.push(XE.MVVM.watch(() => {
            return this.a;
        }, () => {
            // console.log('this.a');
        }));

        this.disposers.push(XE.MVVM.watch(() => 
        {
            //XE.MVVM.extend(x, { a: '' }, { a: XE.MVVM.reaction.observable.ref })
            return {
                minWidthScale : this.minWidthScale,
                dynamicWidthScale : this.dynamicWidthScale,
                arrowMaxHeightScale : this.arrowMaxHeightScale,
                arrowWingWidthScale : this.arrowWingWidthScale
            };
        }, () => {
            this.change(this.positions, this.minWidthScale, this.dynamicWidthScale, this.arrowMaxHeightScale, this.arrowWingWidthScale);
        }));

        this.disposers.push(XE.MVVM.watch(() => {
            return [...this.positions.map(e => [...e])];
        }, () => {
            this.change(this.positions, this.minWidthScale, this.dynamicWidthScale, this.arrowMaxHeightScale, this.arrowWingWidthScale);
        }));

    }
    
    change( p, minWidthScale, dynamicWidthScale, arrowMaxHeightScale, arrowWingWidthScale){
        
        let positions = [...p];
        const l = positions.length;
        if(l < 2 || isEquals(positions[l-1],positions[l-2])){
            return;
        }
        
        let positionsCartesian3 = positions.map( e=>{
            return Cesium.Cartesian3.fromRadians( ...e, null, new Cesium.Cartesian3())
        });
        
        this._polygonPositions.length = 0;
        

        let last,next,current,hpr=[0,0,0];
        let d1, d2,index,result;
        //(heading heading)
        let centerDirection = (new Array(l)).fill(0).map( ( e, k)=>{

            last = positions[k-1];
            next = positions[k+1];
            current = positions[k];

            d1 = last ? Tool.Math.hpr( current, last, hpr)[0] : 0;
            d2 = next ? Tool.Math.hpr( current, next, hpr)[0] : 0;

            index = d1 && d2 ? 2 : 1;
            //d1d2
            result = (d1+ d2)/index;
            result = d1 > d2 ? result : result - Cesium.Math.PI;
            
            return result < 0 ? result+Cesium.Math.TWO_PI : result;
            
        });

        //
        let length = 0;
        let mileages = [];
        positionsCartesian3.forEach( (e, i)=>{
            last = positionsCartesian3[i-1] || e;
            next = e;
            
            length += Cesium.Cartesian3.distance(last, next);
            mileages.push( length);
        });

        mileages = mileages.map( e=>{
            return length-e;
        })
        
        //mileages.reverse();

        //
        minWidthScale /=2;//
        dynamicWidthScale /= 2;
        const dynamicWidthMax = dynamicWidthScale * length;
        const minWidth = minWidthScale * length;

        let positionsMove = (new Array( mileages.length)).fill(0).map( ( e, k)=>{
            
            return minWidth + dynamicWidthMax * mileages[k] / length;
        });

        //

        let arrowHead = [];
        let notMax =  getArrowHead( length, arrowMaxHeightScale, minWidth, arrowWingWidthScale,
            positionsCartesian3.slice(-2), positions.slice(-2), arrowHead);
        
        //
        let left = [],right = [];
        positions.forEach( (e,k)=>{
            
            if( k==0 || k==l-1 ){

                left.push( Tool.Math.geoMove( e, centerDirection[k] + Cesium.Math.PI_OVER_TWO, positionsMove[k]));
                right.push( Tool.Math.geoMove( e, centerDirection[k] - Cesium.Math.PI_OVER_TWO, positionsMove[k]));
            }else{
                
                left.push( Tool.Math.geoMove( e, centerDirection[k] - Cesium.Math.PI, positionsMove[k]));
                right.push( Tool.Math.geoMove( e, centerDirection[k], positionsMove[k]));
            }
        });
        left.pop();
        right.pop();
        if( notMax){
            left.pop();
            right.pop();
        }
        left.push(arrowHead[0]);
        right.push(arrowHead[arrowHead.length-1]);
        
        let leftCurve = Tool.Math.interpolatePositions(left, l*20, false, []);
        let rightCurve = Tool.Math.interpolatePositions(right, l*20, false, []);
        
        leftCurve.forEach( e=>{
            
            this._polygonPositions.push( e[0],e[1]);
        });
        
        arrowHead.forEach( e=>{
            this._polygonPositions.push( e[0],e[1]);
        })
        

        for(let i = rightCurve.length-1; i >= 0; i--){
            this._polygonPositions.push( rightCurve[i][0], rightCurve[i][1]);
        }
        
        this._polygon.positions = this._polygonPositions;
        this._polygon.height = positions[0][2];

        this._polygonShow = true;
    }
}

GeoArrow.registerType(GeoArrow, 'GeoArrow');

//toJson fromJson

GeoArrow.defaultOptions = {
    // positions: [[0,0,0],[1,0,0],[1,1,0]],
    minWidthScale: 0.06,
    dynamicWidthScale: 0.1,
    arrowMaxHeightScale: 0.15,
    arrowWingWidthScale: 2.0,
};

export default GeoArrow;

//
let index;
function isEquals( left, right){
    if(!left || !right){
        return false;
    }
    index = 0;
    for(let i=0; i<left.length; i++){
        index = left[i] == right[i] ? 0 : 1;
        if(index > 0){
            return false;
        }
    }
    return true;
}