//
export default function getCurve( positions, loop=false, times = 20, result){
    if(positions.length < 3) return;

    //Cartesian3
    let positionsC = positions.map( e=>{
        return Cesium.Cartesian3.fromRadians( ...e, undefined,new Cesium.Cartesian3());
    });
    if(!result){
        result = [];
    }

    let last,current,next;
    last = positionsC[0];
    
    let directions = [];
    let vector1 = new Cesium.Cartesian3();
    let vector2 = new Cesium.Cartesian3();
    let vector, distance1, distance2;
    let controlPositions = [];
    if( loop == true){

        last = positionsC[ positionsC.length-1];
        directions = positionsC.map( ( position, i)=>{
            
            current = position;
            next = positionsC[i+1] || positionsC[0];
            distance1 = Cesium.Cartesian3.distance( last, current);
            distance2 = Cesium.Cartesian3.distance( current, next);

            //
            vector1 = Cesium.Cartesian3.subtract( current, last, vector1);
            vector2 = Cesium.Cartesian3.subtract( next, current, vector2);
            Cesium.Cartesian3.normalize( vector1, vector1);
            Cesium.Cartesian3.normalize( vector2, vector2);
            Cesium.Cartesian3.multiplyByScalar( vector1, distance1, vector1);
            Cesium.Cartesian3.multiplyByScalar( vector2, distance2, vector2);

            vector = Cesium.Cartesian3.add( vector1, vector2, new Cesium.Cartesian3());
            Cesium.Cartesian3.normalize( vector, vector);
            last = current;

            //
            let p1 = Cesium.Cartesian3.multiplyByScalar( vector, -distance1 * 0.3, new Cesium.Cartesian3());
            let p2 = Cesium.Cartesian3.multiplyByScalar( vector, distance2 * 0.3, new Cesium.Cartesian3());
            Cesium.Cartesian3.add( current, p1, p1);
            Cesium.Cartesian3.add( current, p2, p2);
            controlPositions.push( p1, p2);
            return vector;
        });
        controlPositions.push( controlPositions.shift());
        
        
    }else{
        
        //,controlPosition
        doit( positionsC[0], positionsC[1], controlPositions, directions);

        for( let i = 1; i < positionsC.length - 1; i++){
            //
            last = positionsC[i-1];
            current = positionsC[i];
            next = positionsC[i+1];

            distance1 = Cesium.Cartesian3.distance( last, current);
            distance2 = Cesium.Cartesian3.distance( current, next);
            
            //
            vector1 = Cesium.Cartesian3.subtract( current, last, vector1);
            vector2 = Cesium.Cartesian3.subtract( next, current, vector2);
            Cesium.Cartesian3.normalize( vector1, vector1);
            Cesium.Cartesian3.normalize( vector2, vector2);
            Cesium.Cartesian3.multiplyByScalar( vector1, distance1, vector1);
            Cesium.Cartesian3.multiplyByScalar( vector2, distance2, vector2);

            vector = Cesium.Cartesian3.add( vector1, vector2, new Cesium.Cartesian3());
            Cesium.Cartesian3.normalize( vector, vector);
            last = current;

            //
            let p1 = Cesium.Cartesian3.multiplyByScalar( vector, -distance1 * 0.3, new Cesium.Cartesian3());
            let p2 = Cesium.Cartesian3.multiplyByScalar( vector, distance2 * 0.3, new Cesium.Cartesian3());
            Cesium.Cartesian3.add( current, p1, p1);
            Cesium.Cartesian3.add( current, p2, p2);
            controlPositions.push( p1, p2);
            directions.push( vector);
        }
        doit( positionsC[ positionsC.length-2], positionsC[ positionsC.length-1], controlPositions, directions);
    }
    //bezier
    for( let i = 0; i < positionsC.length; i++){

        let p0 = positionsC[i] || positionsC[positionsC.length-1];
        let p3 = positionsC[i+1] || positionsC[0];
        let p1 = controlPositions[i*2] || controlPositions[controlPositions.length-2];
        let p2 = controlPositions[i*2+1] || controlPositions[controlPositions.length-1];
        result.push(...bezier3( p0, p1, p2, p3, times))
    }

    //resultCartesian3
    result = result.map( point=>{
        let cartographic = Cesium.Cartographic.fromCartesian( point);
        return [cartographic.longitude, cartographic.latitude, cartographic.height];
    });
    return result;
}

let bezier3 = function(p0, p1, p2, p3, times = 20){
    let result = [];
    //
    let t1,t2,t3,tt1,tt2,tt3;
    result.push( p0);
    let point;
    for( let i = 1; i < times; i++){
        t1 = i / times;
        t2 = t1 * t1;
        t3 = t2 * t1;
        tt1 = 1 - t1;
        tt2 = tt1 * tt1;
        tt3 = tt2 * tt1;
        point = new Cesium.Cartesian3();
        point.x = p0.x * tt3 + 3 * p1.x * tt2 * t1 + 3 * p2.x * tt1 * t2 + p3.x * t3;
        point.y = p0.y * tt3 + 3 * p1.y * tt2 * t1 + 3 * p2.y * tt1 * t2 + p3.y * t3;
        point.z = p0.z * tt3 + 3 * p1.z * tt2 * t1 + 3 * p2.z * tt1 * t2 + p3.z * t3;

        result.push( point);
    }
    result.push( p3);

    return result;
}

let doit = function( last, next, controlPositions, directions){

    let distance = Cesium.Cartesian3.distance( next, last);
    vector = Cesium.Cartesian3.subtract( next, last, new Cesium.Cartesian3());
    Cesium.Cartesian3.normalize( vector, vector);

    let p1 = Cesium.Cartesian3.multiplyByScalar( vector, distance/3, new Cesium.Cartesian3());
    let p2 = Cesium.Cartesian3.multiplyByScalar( vector, 2*distance/3, new Cesium.Cartesian3());
    Cesium.Cartesian3.add( last, p1, p1);
    Cesium.Cartesian3.add( last, p2, p2);
    controlPositions.push( p1, p2);
    directions.push( vector);
}