import PlotPolygonBase from './base/PlotPolygonBase';
import getCurve from './math/getCurve.js';
const Tool = XE.Tool;

class GeoSmoothPolygon extends PlotPolygonBase {
    constructor(earth, guid) {
        super(earth, guid);

        this._pgPositions = [];
        this.disposers.push(
            XE.MVVM.watch(
            () => {
                return [...this.positions.map(e => [...e])];
            }, () => {
                //
                let lastP;
                let positions = this.positions.filter( position=>{
                    let isReturn = true;
                    if( lastP){
                        if( lastP[0] == position[0] && lastP[1] == position[1] && lastP[2] == position[2]){
                            isReturn = false;
                        }
                    }
                    if( isReturn) return position;
                })

                const l = positions.length;
                if (l <= 2) {
                    return;
                }
                if( isEquals(positions[l-1],positions[l-2])) return;

                this._pgPositions.length = 0;

                //positions
                let controlPositions = [];
                
                positions.forEach( e => {
                    controlPositions.push(e);
                });
                
                //let curve = Tool.Math.interpolatePositions(controlPositions, (l+1)*20 -1, true, []);
                //let curve = hermite(controlPositions, (l+1)*20, true, []);
                let curve = getCurve(positions, true, 20);
                curve.forEach(e => {
                    this._pgPositions.push(e[0], e[1]);
                });

                this._polygon.positions = this._pgPositions;
                this._polygon.height = positions[0][2];
            })
        );
    }
}

GeoSmoothPolygon.registerType(GeoSmoothPolygon, "GeoSmoothPolygon");

export default GeoSmoothPolygon;

//
let index;
function isEquals( left, right){
    if(!left || !right){
        return false;
    }
    index = 0;
    for(let i=0; i<left.length; i++){
        index = left[i] == right[i] ? 0 : 1;
        if(index > 0){
            return false;
        }
    }
    return true;
}
