//hermite

function hermite( positions, splitNum, loop=false, result){
    if(positions.length < 3) return;

    //Cartesian3
    let positionsC = positions.map( e=>{
        return Cesium.Cartesian3.fromRadians( ...e, undefined,new Cesium.Cartesian3());
    });
    if(!result){
        result = [];
    }

    let last,current,next;
    last = positionsC[0];
    let distance;
    let length = 0;
    let distances = positionsC.map( position=>{
        distance = Cesium.Cartesian3.distance( last, position);
        length += distance;
        last = position;
        return distance;
    });
    let directions = [];
    let vector1 = new Cesium.Cartesian3();
    let vector2 = new Cesium.Cartesian3();
    let vector;
    if( loop == true){

        distances.splice( 0, 1);
        distances.push( Cesium.Cartesian3.distance( positionsC[0], positionsC[positionsC.length-1]))
        length += distances[0];

        last = positionsC[ positionsC.length-1];
        directions = positionsC.map( (position, i)=>{
            
            current = position;
            next = positionsC[i+1] || positionsC[0];

            //
            vector1 = Cesium.Cartesian3.subtract( current, last, vector1);
            vector2 = Cesium.Cartesian3.subtract( next, current, vector2);

            last = current;
            vector = Cesium.Cartesian3.add( vector1, vector2, new Cesium.Cartesian3());
            return Cesium.Cartesian3.multiplyByScalar( vector, 0.6, vector);
        });

        //splitNum
        let distanceBetweenTwoPoints = length / splitNum;
        //
        let preDistance = 0;
        let num;
        let pointsNumArr = distances.map( distance=>{
            num = parseInt((distance + preDistance) / distanceBetweenTwoPoints);
            preDistance = distance + preDistance - num * distanceBetweenTwoPoints;
            return num;
        });

        _hermite( directions, positionsC, pointsNumArr, result);
    }else{

        //
        distances.splice( 0, 1);
        
        last = undefined;
        directions = positionsC.map( (position, i)=>{
            current = position;
            next = positionsC[i+1];
            if( last && next){

                vector1 = Cesium.Cartesian3.subtract( current, last, vector1);
                vector2 = Cesium.Cartesian3.subtract( next, current, vector2);
                vector = Cesium.Cartesian3.add( vector1, vector2, new Cesium.Cartesian3());
            }else{
                
                vector = last ? Cesium.Cartesian3.subtract( current, last, new Cesium.Cartesian3()) : 
                Cesium.Cartesian3.subtract(next, current, new Cesium.Cartesian3());
            }

            last = current;
            
            return Cesium.Cartesian3.multiplyByScalar( vector, 0.5, vector);
        });

        //splitNum
        let distanceBetweenTwoPoints = length / splitNum;
        //
        let preDistance = 0;
        let num;
        let pointsNumArr = distances.map( distance=>{
            num = parseInt((distance + preDistance) / distanceBetweenTwoPoints);
            preDistance = distance + preDistance - num * distanceBetweenTwoPoints;
            return num;
        });
        _hermite( directions, positionsC, pointsNumArr, result);
    }

    result = result.map( e=>{
        let cartesian = Cesium.Cartographic.fromCartesian(e, undefined, new Cesium.Cartographic());
        return [cartesian.longitude, cartesian.latitude, cartesian.height];
    })

    return result;
}

function _hermite( vectors, positionsC, pointsNumArr, result){

    let t3,t2,t, step;
    let a1,a2,a3,a4;
    let p1,p2,vector1,vector2;
    result.push( positionsC[0]);

    p1 = positionsC[0];
    vector1 = vectors[0];
    pointsNumArr.forEach( (num, k) =>{
        //
        p2 = positionsC[k+1] || positionsC[0];
        vector2 = vectors[k+1] || vectors[0];
        step = 1.0 / num;
        for( let i = 1; i < num; i++){
            t = i * step;
            
            t2 = t * t;
            t3 = t2 * t;

            a1 = 2 * t3 - 3 * t2 + 1;
            a2 = -2 * t3 + 3 * t2;
            a3 = t3 - 2 * t2 + t;
            a4 = t3 - t2;
            let point = new Cesium.Cartesian3();
            point.x = a1 * p1.x + a2 * p2.x + a3 * vector1.x + a4 * vector2.x;
            point.y = a1 * p1.y + a2 * p2.y + a3 * vector1.y + a4 * vector2.y;
            point.z = a1 * p1.z + a2 * p2.z + a3 * vector1.z + a4 * vector2.z;
            result.push( point);
        }
        p1 = p2;
        vector1 = vector2;
        result.push( p2);
    });
}
