/**
 * burst
 */
import getImage from './getImage.js';

let gravityScratch = new Cesium.Cartesian3();
const Tool = XE.Tool;

class ParticleSystemFireWork extends XE.Core.XbsjCzmObj{
    constructor( earth, guid){

        super( earth, guid);
        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionEditing(this._earth, this, {
            positionEditingProperty: 'editing',
        }));

        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionPicking(this._earth, this, {
            positionPickingProperty: 'creating',
        }));

        this._viewer = earth.czm.viewer;
        this._modelMatrix = this._modelMatrix || Cesium.Matrix4.IDENTITY;
        this._position = new Cesium.Cartesian3();

        this._particleSystem = new Cesium.ParticleSystem({
            image: getImage(),
            show: this.show && this.enabled,

            startColor: {red: this.startColor[0],green: this.startColor[1],blue: this.startColor[2],alpha: this.startColor[3]},
            endColor:   {red: this.endColor[0],green: this.endColor[1],blue: this.endColor[2],alpha: 0},

            particleLife: this.particleLife,

            speed: this.radius / this.particleLife || 0,

            imageSize: new Cesium.Cartesian2(7,7),

            bursts:  [new Cesium.ParticleBurst({
                time: 0.0,
                minimum: 400,
                maximum: 400
            })],

            emissionRate: 0,

            lifetime: this.particleLife + this.burstInterval || 2.0,
            modelMatrix: this._modelMatrix,

            emitter: new Cesium.SphereEmitter(0.1),
        });

        this._viewer.scene.primitives.add( this._particleSystem);

        this.disposers.push( ()=>{
            if ( this._particleSystem){
                this._viewer.scene.primitives.remove( this._particleSystem);
                this._particleSystem = undefined;
            }
        })

        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                startColor: [...this.startColor],
                endColor:   [...this.endColor],
                show:       this.show && this.enabled,
            }
        }, () => this.updateColor() ));
        
        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                radius:         this.radius,
                particleLife:   this.particleLife,
                burstInterval:  this.burstInterval,
            }
        }, () => this.updateInitialVelocity() ));

        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                position:  [...this.position],
            }
        }, () => this.updateModelMatrix()));
    }

    updateColor(){
        this._particleSystem.startColor = {red: this.startColor[0],green: this.startColor[1],blue: this.startColor[2],alpha: this.startColor[3]};
        this._particleSystem.endColor = {red: this.endColor[0],green: this.endColor[1],blue: this.endColor[2],alpha: this.endColor[3]};
        this._particleSystem.show = this.show && this.enabled;
    }

    updateModelMatrix(){
        Cesium.Cartesian3.fromRadians( ...this.position, null, this._position);
        
        if( !this._position.x && !this._position.y && !this._position.z) return;

        this._modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame( this._position);
        this._particleSystem.modelMatrix = this._modelMatrix;
    }

    updateInitialVelocity(){

        this._initialVelocity = this.radius / this.particleLife;
        this._particleSystem.lifetime = this.particleLife + this.burstInterval;
        this._particleSystem.minimumParticleLife = this.particleLife;
        this._particleSystem.maximumParticleLife = this.particleLife;
        // this._particleSystem.bursts.pop();
        // this._particleSystem.bursts.push( new Cesium.ParticleBurst({
        //     time: 0.0,
        //     minimum: 400,
        //     maximum: 400
        // }));
        
        this._particleSystem.minimumSpeed = this._initialVelocity;
        this._particleSystem.maximumSpeed = this._initialVelocity;
    }

    flyTo(){

        this.boundingSphere = new Cesium.BoundingSphere( this._position, this.radius);
        this._viewer.camera.flyToBoundingSphere( this.boundingSphere ,{
            duration: 2,
        });
    }

}

ParticleSystemFireWork.defaultOptions = {
    //
    show: true,
    radius: 200,
    particleLife: 0.8,//
    burstInterval: 1.0,//

    startColor: [0.75, 0.0, 0.8, 1.0],
    endColor:   [0.75, 0.0, 0.8, 0.0],//0
    position:   [1.7, 0.6666896940162155, 100],
}
ParticleSystemFireWork.registerType(ParticleSystemFireWork, 'ParticleSystemFireWork');

export default ParticleSystemFireWork;

