import getImage from './getImage.js';
const Tool = XE.Tool;
/**
 * 
 * modelMatrix()
 * modelMatrix
 */
class ParticleSystemTails extends XE.Core.XbsjCzmObj{

    constructor(earth, guid){
        super(earth, guid);
        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionEditing(this._earth, this, {
            positionEditingProperty: 'positionEditing',
        }));

        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionPicking(this._earth, this, {
            positionPickingProperty: 'creating',
        }));
        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerRotationEditing(this._earth, this, {
            rotationEditingProperty: 'rotationEditing',
        }));

        this._viewer = earth.czm.viewer;

        this._particleSystemList = [];
        this._modelMatrix = Cesium.Matrix4.clone(Cesium.Matrix4.IDENTITY, new Cesium.Matrix4());
        this._inverseModelMatrix = Cesium.Matrix4.clone(Cesium.Matrix4.IDENTITY, new Cesium.Matrix4());
        this._position = new Cesium.Cartesian3();
        this._colors = [];

        this.disposers.push( ()=>{
            this._particleSystemList = this._particleSystemList.map(e => {
                if(e){
                    this._viewer.scene.primitives.remove( e);
                    e = undefined;
                    return e;
                }
            });
            this._particleSystemList.length = 0;
        });

        this.disposers.push(XE.MVVM.watch(() => {
            
            return [...this.position,...this.rotation];
        }, () => this.updateMatrix()));

        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                show: this.show && this.enabled,
                color:[...this.colors]};
        }, () => this.updateColors()));
        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                emissionRate: this.emissionRate,
                particleSize: this.particleSize
            };
        }, () => this.updateCommon()));

        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                radius:         this.radius,
                rotate:         this.rotate,
                height:         this.height,
                particleLife:   this.particleLife,
            };
        }, () => this.update()));

        this.disposers.push(XE.MVVM.watch( ()=>{

            return this.particleNumber
        }, ()=> {
            this.update();
            this.updateParticleSystemList();
        }))

        this.updateParticleSystemList();
    }

    updateCommon(){
        this._particleSystemList.forEach( particleSystem=>{

            particleSystem.emissionRate = this.emissionRate;
            particleSystem.minimumImageSize.x = this.particleSize;
            particleSystem.minimumImageSize.y = this.particleSize;
            particleSystem.maximumImageSize.x = this.particleSize;
            particleSystem.maximumImageSize.y = this.particleSize;
        })
    }

    updateColors(){
        this._colors.length = 0;
        for(let i = 0; i < this.colors.length; i+=4){
            this._colors.push( new Cesium.Color( this.colors[i], this.colors[i+1], this.colors[i+2], this.colors[i+3]));
        }
        let length = this._colors.length;
        let i = 0;
        
        this._particleSystemList.forEach( ( e, k)=>{
            i = k % length;
            e.startColor = this._colors[i];
            e.endColor = this._colors[i].withAlpha(0.0);
            e.show = this.show && this.enabled;
        })
    }

    update(){
        this._radiuStep = this.radius / this.particleLife;
        this._rotateSpeed = this.rotate / this.particleLife;
        this._speed = this.height / this.particleLife;

        this.changeUpdateCallBack();
    }

    updateMatrix(){
        let rotateMatrix = new Cesium.Matrix4();
        Cesium.Cartesian3.fromRadians( ...this.position, null, this._position);
        
        if( !this._position.x && !this._position.y && !this._position.z) return;

        this._modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame( this._position);

        let hpr = new Cesium.HeadingPitchRoll(...this.rotation);
        let quaternion = Cesium.Quaternion.fromHeadingPitchRoll(hpr, new Cesium.Quaternion());

        rotateMatrix = Cesium.Matrix4.fromTranslationQuaternionRotationScale(
            Cesium.Cartesian3.ZERO, quaternion, new Cesium.Cartesian3(1,1,1), rotateMatrix);
        
        Cesium.Matrix4.multiply(this._modelMatrix, rotateMatrix, this._modelMatrix);
        this._inverseModelMatrix = Cesium.Matrix4.inverse( this._modelMatrix, this._inverseModelMatrix);

        this._particleSystemList.forEach( (e, i)=>{

            e.modelMatrix = this._modelMatrix;
        });
        this.changeUpdateCallBack();
    }

    changeUpdateCallBack(){
        this._particleSystemList.forEach( (e, i)=>{

            e.updateCallback = this.updateCallback(this._radiuStep, this._rotateSpeed, 
                Cesium.Math.TWO_PI * i/this.particleNumber, this._speed, this._inverseModelMatrix, this._modelMatrix);
        });
    }

    //rotateSpeedthis._modelMatrixthis._modelMatrix
    updateCallback(radiuStep, rotateSpeed, angle, speed, inverseModelMatrix, modelMatrix){
        let position = new Cesium.Cartesian3();
        return (particle, dt)=>{
            dt = Cesium.Math.clamp(dt, 0.0, 0.05);
            position = Cesium.Matrix4.multiplyByPoint( inverseModelMatrix, particle.position, position);
            angle += dt * rotateSpeed;
            angle %= Cesium.Math.TWO_PI;

            position.x += Math.cos( angle) * radiuStep * dt;
            position.y += Math.sin( angle) * radiuStep * dt;
            position.z -= speed * dt;

            particle.position = Cesium.Matrix4.multiplyByPoint( modelMatrix, position, particle.position);
        }
    }

    updateParticleSystemList(){
        this._particleSystemList = this._particleSystemList.map(e => {
            if(e){
                this._viewer.scene.primitives.remove( e);
                e = undefined;
                return e;
            }
        });
        this._particleSystemList.length = 0;

        let index; 
        for(let i = 0 ; i < this.particleNumber; ++i){
            index = i % this._colors.length;
            
            let force = this.updateCallback(this._radiuStep, this._rotateSpeed, Cesium.Math.TWO_PI * i/this.particleNumber,
                 this._speed, this._inverseModelMatrix, this._modelMatrix);
            //let force = this.forceFunction( options, i);
            this._particleSystemList.push(

                new Cesium.ParticleSystem({
                    image: getImage(),
                    show: this.show && this.enabled,
        
                    startColor: this._colors[index],
                    endColor: this._colors[index].withAlpha(0.0),

                    particleLife: this.particleLife,
        
                    speed: 0.000005,
        
                    imageSize: new Cesium.Cartesian2(
                        this.particleSize,
                        this.particleSize
                    ),
        
                    emissionRate: this.emissionRate,
                    modelMatrix: this._modelMatrix,

                    lifetime: 0.1,
        
                    emitter: new Cesium.CircleEmitter(0.01),
                    updateCallback: force,
                })
            );
            
        }
        this._particleSystemList.forEach( e=>{
            this._viewer.scene.primitives.add(e);
        })
    }

    flyTo(){
        this.boundingSphere = new Cesium.BoundingSphere( this._position, Math.sqrt( Math.pow(this.radius, 2) * 2, Math.pow(this.height, 2)) );
        this._viewer.camera.flyToBoundingSphere( this.boundingSphere ,{
            duration: 2,
        });
    }

    toBeCometTail(){
        this.colors = [...options.comet.colors];
        this.rotate = options.comet.rotate;
    }

    toBeRocketThruster(){
        this.colors = [...options.rocket.colors];
        this.rotate = options.rocket.rotate;
    }
}

ParticleSystemTails.defaultOptions = {

    show: true,
    particleSize: 15,//pixel
    
    emissionRate:30,//(/)
    particleNumber:100,//()
    
    position: [1.67, 0.6, 50],//()
    rotation: [0,0,0],//()
    colors:[0.6, 0.6, 0.6, 1.0, 0.6, 0.6, 0.9, 0.9, 0.5, 0.5, 0.7, 0.5],//0.0
    
    radius: 30,//(ratate)m
    rotate: 0.01,//
    height: 50,//m
    particleLife: 3.5,//s
    
}

const options={
    rocket:{
        colors: [1.0, 0.5, 0.05, 1.0 , 0.9, 0.6, 0.01, 1.0, 0.8, 0.05, 0.09, 1.0 , 1.0, 0.05, 0.09, 1.0],
        rotate: 1.0
    },
    comet:{
        colors: [0.6, 0.6, 0.6, 1.0, 0.6, 0.6, 0.9, 0.9, 0.5, 0.5, 0.7, 0.5],
        rotate: 0.01
    }
}
ParticleSystemTails.registerType(ParticleSystemTails, 'ParticleSystemTails');

export default ParticleSystemTails;