

class PlotPolygonBase extends XE.Obj.PlotBase {
    constructor(earth, guid) {
        super(earth, guid);

        this._onlyMove = false;
        this._fixedPositionsNum = undefined;

        XE.MVVM.extend(this, {
            _polygonShow: true, // 
        });

        this._polygon = new XE.Obj.Polygon(earth);
        this.disposers.push(() => {
            this._polygon = this._polygon && this._polygon.destroy();
        });
        this._polygon.show = this._polygonShow && this.show;

        XE.Earth.Interaction.Picking.registerPickingParent(this._polygon, this);
        this._position = Cesium.Cartesian3.fromRadians(0, 0, 0);
        this.area = 0
        let dis = Cesium.Cartesian3.distance(earth.czm.viewer.camera.positionWC, this._position);
        this._billboardEnt = earth.czm.viewer.entities.add({
            name: '',
            position: this._position,
            // point: {
            //     pixelSize: 5.0,
            //     color: Cesium.Color.YELLOW,
            //     outlineColor: Cesium.Color.BLACK,
            //     outlineWidth: 1.0,
            // },
            label: {
                text: this.area + '',
                translucencyByDistance: new Cesium.NearFarScalar(
                    dis * 5.0,
                    2.0,
                    dis * 30.0,
                    0
                ),
                style: Cesium.LabelStyle.FILL_AND_OUTLINE,
                font: '35px ',
                fillColor: Cesium.Color.WHITE.withAlpha(0.9), // withAlpha
                outlineColor: Cesium.Color.BLACK.withAlpha(0.9),
                outlineWidth: 6,
                scale: 0.5,
                disableDepthTestDistance: 1000000000,
                pixelOffset: new Cesium.Cartesian2(0, -10),
                verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
                // distanceDisplayCondition: distanceDisplayCondition,
                // translucencyByDistance: translucencyByDistance,
                // showBackground:true,
                // backgroundColor:new Cesium.Color.fromCssColorString("rgba(0, 0, 0, 0.7)"),
                // backgroundPadding:new Cesium.Cartesian2(10, 10),
            }
        });
        const updatePolygon = () => {
            this._polygon.show = this._polygonShow && this.show;
            this._polygon.width = this.width;
            this._polygon.color = this.color;
            this._polygon.ground = this.ground;

            this._polygon.outline.width = this.outlineWidth;
            this._polygon.outline.color = this.outlineColor;
            this._polygon.outline.show = this.outlineShow;
            this._polygon.depthTest = this.depthTest;
            this._polygon.extrudedHeight = this.extrudedHeight;
            this._polygon.minShowHeight = this.minShowHeight;
            this._polygon.maxShowHeight = this.maxShowHeight;
            this.area = this._polygon._totalArea;
            let area = this._polygon._totalArea;
            const cartesians = this.positions.map((p) =>
                Cesium.Cartesian3.fromRadians(...p)
            );
            let bs = Cesium.BoundingSphere.fromPoints(cartesians);
            this._billboardEnt.show = this.showArea && this.show;
            this._billboardEnt.position = bs.center;
            if (this.xbsjType == 'GeoRectangle' || this.xbsjType == 'GeoCircle') {
                if (this.positions.length > 1 && area) {
                    this._billboardEnt.label.text = changeAreaUnit(area)
                }
            } else {
                if (this.positions.length > 2 && area) {
                    this._billboardEnt.label.text = changeAreaUnit(area)
                }
            }

        }

        function changeAreaUnit(area) {
            if (area > 1000000) {
                return (area / 1000000).toFixed(3) + ''
            } else if (area > 10000) {
                return (area / 10000).toFixed(3) + ''
            } else if (area > 100) {
                return (area / 100).toFixed(3) + ''
            } else {
                return area.toFixed(3) + ''
            }
        }

        updatePolygon();
        this.disposers.push(XE.MVVM.watch(() => {
            return {
                show: this._polygonShow && this.show,
                width: this.width,
                color: [...this.color],
                ground: this.ground,
                outlineWidth: this.outlineWidth,
                outlineColor: this.outlineColor,
                outlineShow: this.outlineShow,
                depthTest: this.depthTest,
                extrudedHeight: this.extrudedHeight,
                minShowHeight: this.minShowHeight,
                maxShowHeight: this.maxShowHeight,
                showArea: this.showArea,
                positions: [...this.positions],
            }
        }, () => updatePolygon()),
            () => {
                earth.czm.viewer.entities.remove(this._billboardEnt);
            }
        );
    }

    get onlyMove() {
        return this._onlyMove;
    }

    get fixedPositionsNum() {
        return this._fixedPositionsNum;
    }

    flyTo() {
        this._polygon.flyTo();
    }
}

PlotPolygonBase.defaultOptions = {
    positions: [],
    show: true,
    color: [0, 1, 0, 0.1],
    outlineWidth: 2.0,
    outlineColor: [0, 1, 0, 1],
    outlineShow: true,
    depthTest: false, // 
    extrudedHeight: null,
    minShowHeight: -Number.MAX_VALUE,
    maxShowHeight: Number.MAX_VALUE,
    showArea: false,//
};

export default PlotPolygonBase;
