let Tool = XE.Tool;
//
export default function getArrowHead( length, arrowMaxHeightScale, minWidth, arrowWingWidthScale, positionsCartesian3, positionsRadian, arrowHead){
    //0.3
    const arrowWingUnderScale = 0.3;
    //
    let height = length * arrowMaxHeightScale;
    let topHeight = height * (1 - arrowWingUnderScale);
     
    let distance = Cesium.Cartesian3.distance( positionsCartesian3[0], positionsCartesian3[1]);
    let notMax = false;
    if(distance < topHeight){
 
        topHeight = distance;
        height = topHeight / (1 - arrowWingUnderScale);
        notMax = true;
    }
    //let bottomHeight = topHeight * (1 - arrowWingUnderScale) / arrowWingUnderScale;
 
    //heading
    let heading = Tool.Math.hpr( positionsRadian[1], positionsRadian[0], [0,0,0])[0];
    let waistCenter = Tool.Math.geoMove( positionsRadian[1], heading, topHeight, [0,0,0]);
    let wingCenter = Tool.Math.geoMove( positionsRadian[1], heading, height, [0,0,0]);
 
    //
    let waistCenterHeading = Tool.Math.hpr( waistCenter, positionsRadian[1], [0,0,0])[0];
    let wingCenterHeading = Tool.Math.hpr( wingCenter, positionsRadian[1], [0,0,0])[0];
 
    //
    arrowHead[0] = Tool.Math.geoMove( waistCenter, waistCenterHeading - Cesium.Math.PI_OVER_TWO, minWidth, [0,0,0]);
    arrowHead[4] =  Tool.Math.geoMove( waistCenter, waistCenterHeading + Cesium.Math.PI_OVER_TWO, minWidth, [0,0,0]);
    arrowHead[1] = Tool.Math.geoMove( wingCenter, wingCenterHeading - Cesium.Math.PI_OVER_TWO, minWidth*arrowWingWidthScale, [0,0,0]);
    arrowHead[3] = Tool.Math.geoMove( wingCenter, wingCenterHeading + Cesium.Math.PI_OVER_TWO, minWidth*arrowWingWidthScale, [0,0,0]);
    arrowHead[2] = [...positionsRadian[1]];
 
    return notMax;
}