//

let gravityScratch = new Cesium.Cartesian3();
const Tool = XE.Tool;
class ParticleSystem extends XE.Core.XbsjCzmObj{
    
    constructor( earth, guid){
        super(earth, guid);
        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionEditing(this._earth, this, {
            positionEditingProperty: 'positionEditing',
        }));

        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerPositionPicking(this._earth, this, {
            positionPickingProperty: 'creating',
        }));
        this.disposers.push(XE.Earth.Interaction.InteractionProperty.registerRotationEditing(this._earth, this, {
            rotationEditingProperty: 'rotationEditing',
        }));
        this._viewer = earth.czm.viewer;
        
        let position = new Cesium.Cartesian3();
        Cesium.Cartesian3.fromRadians( ...this.position, null, position);
        this._modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame( position);
        this._updateCallback = ()=>{};

        this._particleSystem = new Cesium.ParticleSystem({
            
            image: this.image,
            show: this.show && this.enabled,

            startColor: new Cesium.Color( this.startColor[0], this.startColor[1], this.startColor[2], this.startColor[3]),
            endColor: new Cesium.Color( this.endColor[0], this.endColor[1], this.endColor[2], this.endColor[3]),

            startScale: this.startScale,
            endScale: this.endScale,

            minimumParticleLife: this.minimumParticleLife,
            maximumParticleLife: this.maximumParticleLife,

            minimumSpeed: this.minimumSpeed,
            maximumSpeed: this.maximumSpeed,

            imageSize: new Cesium.Cartesian2(
                this.particleSize,
                this.particleSize
            ),

            emissionRate: this.emissionRate,

            lifetime: 16.0,
            modelMatrix: this._modelMatrix,

            emitterModelMatrix: Cesium.Matrix4.IDENTITY,

            emitter: new Cesium.CircleEmitter(2),

            updateCallback: this._updateCallback
        });
        this._viewer.scene.primitives.add( this._particleSystem);

        this.disposers.push(() => {
            if (this._particleSystem) {
                this._viewer.scene.primitives.remove(this._particleSystem)
                this._particleSystem = undefined;
            }
        });
        
        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                startColor:     [...this.startColor],
                endColor:       [...this.endColor],
                image:          this.image,
                emissionRate:   this.emissionRate,
                maximumSpeed:   this.maximumSpeed,
                minimumSpeed:   this.minimumSpeed,
                
                minimumImageSize:   this.minimumImageSize,
                maximumImageSize:   this.maximumImageSize,
                minimumParticleLife: this.minimumParticleLife,
                maximumParticleLife: this.maximumParticleLife,
                show: this.show && this.enabled,
            }
        }, () => this.update()));

        this.disposers.push(XE.MVVM.watch(() => {
            
            return {
                image:          this.image,
            }
        }, () => this.updateParticleSystem()));

        this.disposers.push(XE.MVVM.watch( ()=>{

            return {
                particleSize:   this.particleSize,
            }
        }, ()=>  this.updateParticleSize() ));

        this.disposers.push(XE.MVVM.watch(() => {
            
            return [...this.position,...this.rotation];
        }, () => this.updateMatrix()));

        this.disposers.push(XE.MVVM.watch( ()=>{
            return {gravity: this.gravity}
        }, ()=>{
            this._particleSystem.updateCallback = this.updateGravity(this.gravity);
        }));
        
        this.disposers.push(XE.MVVM.watch( ()=>{
            return {
                emitterRadius:  this.emitterRadius,
                emitterAngle:   this.emitterAngle,
                emitterType:    this.emitterType
            }
        }, ()=>this.updateEmitter() ));
    }

    toBeFountain(){
        let param = params['fountain'];
        this.changeShowType(param);
    }
    toBeSmoke(){
        let param = params['smoke'];
        this.changeShowType(param);
    }
    changeShowType(param){
        Object.keys( param).forEach( key=>{
            this[key] = param[key];
        });
    }

    updateMatrix(){
        let position = new Cesium.Cartesian3();
        let rotateMatrix = new Cesium.Matrix4();
        Cesium.Cartesian3.fromRadians( ...this.position, null, position);

        if( !position.x && !position.y && !position.z) return;

        this._modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame( position);

        let hpr = new Cesium.HeadingPitchRoll(...this.rotation);
        let quaternion = Cesium.Quaternion.fromHeadingPitchRoll(hpr, new Cesium.Quaternion());

        rotateMatrix = Cesium.Matrix4.fromTranslationQuaternionRotationScale(
            Cesium.Cartesian3.ZERO, quaternion, new Cesium.Cartesian3(1,1,1), rotateMatrix);
        
        Cesium.Matrix4.multiply(this._modelMatrix, rotateMatrix, this._modelMatrix);
        this._particleSystem.modelMatrix = this._modelMatrix;
    }

    updateParticleSize(){

        if( this._particleSystem.imageSize){
            
            this._particleSystem.imageSize.x = this.particleSize;
            this._particleSystem.imageSize.y = this.particleSize;

            this._particleSystem.maximumImageSize.x = this.particleSize;
            this._particleSystem.maximumImageSize.y = this.particleSize;
            this._particleSystem.minimumImageSize.x = this.particleSize;
            this._particleSystem.minimumImageSize.y = this.particleSize;
        }else{
            this._particleSystem.imageSize = new Cesium.Cartesian2( this.particleSize, this.particleSize);
        }
    }

    updateParticleSystem(){

        //let updateCallback = this._particleSystem.updateCallback;
        if(this._particleSystem){

            this._viewer.scene.primitives.remove( this._particleSystem);
            this._particleSystem = undefined;
        }
        this._particleSystem = new Cesium.ParticleSystem({
            
            image: this.image,
            show: this.show && this.enabled,

            startColor: {red: this.startColor[0],green: this.startColor[1],blue: this.startColor[2],alpha: this.startColor[3]},
            endColor: {red: this.endColor[0],green: this.endColor[1],blue: this.endColor[2],alpha: this.endColor[3]},

            startScale: this.startScale,
            endScale: this.endScale,

            minimumParticleLife: this.minimumParticleLife,
            maximumParticleLife: this.maximumParticleLife,

            minimumSpeed: this.minimumSpeed,
            maximumSpeed: this.maximumSpeed,

            imageSize: new Cesium.Cartesian2(
                this.particleSize,
                this.particleSize
            ),

            emissionRate: this.emissionRate,

            lifetime: 16.0,
            modelMatrix: this._modelMatrix,

            emitterModelMatrix: Cesium.Matrix4.IDENTITY,

            emitter: this._emitter,

            updateCallback: this._updateCallback
        });
        this._viewer.scene.primitives.add( this._particleSystem);
    }

    //this._particleSystem
    update(){
        this._particleSystem.startColor = {red: this.startColor[0],green: this.startColor[1],blue: this.startColor[2],alpha: this.startColor[3]}//Cesium.Color.fromBytes(...this.startColor);
        this._particleSystem.endColor = {red: this.endColor[0],green: this.endColor[1],blue: this.endColor[2],alpha: this.endColor[3]}//Cesium.Color.fromBytes(...this.endColor);
        
        this._particleSystem.startScale = this.startScale;
        this._particleSystem.endScale = this.endScale;
        this._particleSystem.emissionRate = this.emissionRate;
        this._particleSystem.minimumSpeed = this.minimumSpeed;
        this._particleSystem.maximumSpeed = this.maximumSpeed;

        this._particleSystem.minimumParticleLife = this.minimumParticleLife;
        this._particleSystem.maximumParticleLife = this.maximumParticleLife;
        this._particleSystem.show = this.show && this.enabled;
    }

    updateGravity( gravity){
        return this._updateCallback = (p, dt)=>{
            Cesium.Cartesian3.normalize(p.position, gravityScratch);
            Cesium.Cartesian3.multiplyByScalar(
                gravityScratch,
                gravity * dt,
                gravityScratch
            );
  
            p.velocity = Cesium.Cartesian3.add(
                p.velocity,
                gravityScratch,
                p.velocity
            );
        }
    }
    
    updateEmitter(){

        let params;
        if(this.emitterType != 'BoxEmitter'){

            params = this.emitterType == 'ConeEmitter' ? this.emitterAngle : this.emitterRadius;
        }else{

            params = new Cesium.Cartesian3( ...(new Array(3).fill(this.emitterRadius)));
        }
        if(this._particleSystem){

            this._particleSystem.emitter = new Cesium[this.emitterType](params);
            this._emitter = this._particleSystem.emitter;
        }else{
            this._emitter = new Cesium[this.emitterType](params);
            return this._emitter;
        }
    }

    //compute a boundingSphere from particlePool and let camera fly to there
    flyTo(){
        if( this._particleSystem){
            //particleSystempartileSytem
            let speed = Math.abs(this._particleSystem.minimumSpeed) > Math.abs(this._particleSystem.maximumSpeed) ?
                        Math.abs(this._particleSystem.minimumSpeed) : Math.abs(this._particleSystem.maximumSpeed);
            let time = Math.abs(this._particleSystem.minimumParticleLife) > Math.abs(this._particleSystem.maximumParticleLife) ?
                        Math.abs(this._particleSystem.minimumParticleLife) : Math.abs(this._particleSystem.maximumParticleLife);
            let center = new Cesium.Cartesian3();
            Cesium.Matrix4.getColumn(this._particleSystem.modelMatrix, 3, center);
            let radius = speed * time;
            let boundingSphere = new Cesium.BoundingSphere( center, radius);
            this._viewer.camera.flyToBoundingSphere( boundingSphere,{
                duration: 2,
            });
        }
    }

    // destroy(){
    //     if(this._particleSystem){
    //         this._viewer.scene.primitives.remove( this._particleSystem);
    //         this._particleSystem = undefined;
    //     }
    //     super.destroy();
    // }
}

const params = {
    fountain:{
        gravity: 10,
        minimumSpeed: 10,
        maximumSpeed: 15,
        gravity: -10,
        emitterRadius: 0.1,
        emissionRate: 20,
        startColor: [1.0,1.0,1.0,1.0],
        endColor: [1.0,1.0,1.0,0.0],
        emitterType: 'CircleEmitter',
        rotation: [0,Cesium.Math.PI_OVER_TWO,0],
    },
    smoke:{
        startColor: [0.12549, 0.69804, 0.66667, 0.7],
        endColor: [1.0, 1.0, 1.0, 0.0],
    
        rotation: [0,0,0],//rotation[0]
    
        emissionRate: 5.0,
        gravity: 0.0,
        minimumParticleLife: 2,
        maximumParticleLife: 2,
        minimumSpeed: 1.0,
        maximumSpeed: 4.0,
        emitterRadius: 2,
        emitterType: 'CircleEmitter',//BoxEmitterCircleEmitterConeEmitterSphereEmitter
    }
}
//ParticleSystem
ParticleSystem.defaultOptions = {
    show: true,
    enabled: true,
    position: [1.6988083400554626, 0.6666896940162155, 30],
    startScale: 1.0,
    endScale: 5.0,
    particleSize: 25.0,
    image: '../../XbsjEarth-Plugins/plottingSymbol2/assets/SampleData/smoke.png',

    emitterAngle: 1,
    //emitterType: 'CircleEmitter',
    ...params.smoke,
}

ParticleSystem.registerType(ParticleSystem, 'ParticleSystem');

export default ParticleSystem;
